import { Component, OnInit, Input, ViewChild, ViewChildren } from '@angular/core';
import { Role } from '../../../../shared/models/roles';
import { Subscription } from 'rxjs';
import { AuditEvent } from '../../../../shared/models';
import { PerfectScrollbarDirective } from 'ngx-perfect-scrollbar';
import { NgForm } from '@angular/forms';
import { RoleService } from '../../../../shared/services/roles.service';
import { AuditService } from '../../../../shared/services/audit.service';
import { Router, ActivatedRoute } from '@angular/router';
import { NgbModal } from '@ng-bootstrap/ng-bootstrap';
import { SharedAnimations } from '../../../../shared/animations/shared-animations';

@Component({
  selector: 'role-content',
  templateUrl: './content.component.html',
  styleUrls: ['./content.component.scss'],
  animations: [SharedAnimations]
})
export class ContentComponent implements OnInit {

  activeRole: Role = new Role();

  loadAuditDataSub: Subscription;
  roleSelectSub: Subscription;
  roleId: number;
  loadingData = false;


  showHistory = true;
  audits: AuditEvent[];
  auditEventDetail: string[];


  // tslint:disable-next-line:no-input-rename
  @Input('matSidenav') matSidenav;
  @ViewChild(PerfectScrollbarDirective) psContainer: PerfectScrollbarDirective;

  @ViewChildren('msgInput') msgInput;
  @ViewChild('msgForm') msgForm: NgForm;

  constructor(public service: RoleService,
    private auditService: AuditService,
    private router: Router,
    private modalService: NgbModal,
    private route: ActivatedRoute) {

  }

  ngOnInit() {
    this.route.params.subscribe(params => this.roleId = params['id']);
    this.roleSelectSub = this.service.onRoleSelected.subscribe(res => {
      if (res) {
        console.log("service.loadingData", this.service.loadingData)
        this.activeRole = res.role;
        this.showHistory = false;
      }
    });
  }

  ngOnDestroy() {
    if (this.roleSelectSub) { this.roleSelectSub.unsubscribe(); }
  }

  

  edit(role) {
    this.router.navigate(['edit', role.id], { relativeTo: this.route });
  }

  toggleShowAudit() {
    this.showHistory = !this.showHistory;
    if (this.showHistory) {
      this.loadAuditDataSub = this.auditService.getAuditTrails('role', this.activeRole.id.toString())
        .subscribe(res => {
          this.audits = res.events;
        });
    }
  }

  clone(role: Role) {
    this.router.navigate(['clone', role.id], {relativeTo: this.route})
  }

  confirm(content) {
    this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title', centered: true })
    .result.then((result) => {
      console.log("model delete : " + result);
    }, (reason) => {
      console.log("model reason : " + reason);
    });
  }

  open(content, eventId) {
    this.auditService.getAuditEventDetail(eventId).subscribe(
      response => {
        this.auditEventDetail = response.changeDetails;
        this.modalService.open(content, { ariaLabelledBy: 'modal-basic-title' })
      .result.then((result) => {
        console.log(result);
      }, (reason) => {
        console.log('Err!', reason);
      });
      },
      error => {
        console.log(error);
      }
    )
  }
}
