import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable, BehaviorSubject, Subject, of, combineLatest } from 'rxjs';
import { map, switchMap } from 'rxjs/operators';
import { Address, Attachment } from 'src/app/model/common-model';
import { WebApiProvider } from 'src/app/shared/providers';
import { ToastrService } from 'ngx-toastr';
import { Role, RoleResponse } from '../models/roles';


@Injectable({
  providedIn: 'root'
})
export class RoleService {

  public roles: Role[];
  public role: Role;
  public collectionSize: number;

  public loadingCollection: boolean;
  public loadingData: boolean;

  onRoleSelected = new BehaviorSubject<any>(null);
  onAdvancedSearchApplied = new BehaviorSubject<any>(null);
  onClearSearchApplied = new BehaviorSubject<any>(null);


  constructor(private http: HttpClient,
    private toastr: ToastrService,
    private webApiProvider: WebApiProvider) {
  }

  loadRoleData(page: number, size: number): Observable<RoleResponse> {
    return this.getAllRole((page - 1), size);
  }

  create(role: Role) {
    return this.webApiProvider.post('role/v1/', role, true);
  }

  update(role: Role): Observable<Role> {
    return this.webApiProvider.put('role/v1/' + role.id, role);
  }

  delete(id: number) {
    return this.webApiProvider.delete('role/v1/'+ id, true);
  }

 
  getAllRole(page, size): Observable<RoleResponse> {
    return this.webApiProvider.get<any>('role/v1/list/' + page + '/'+ size, true);
  }

  getRoleById(id): Observable<Role> {
    this.loadingData = true;
    return this.getAllRoleById(id)
      .pipe(map(role => {
        this.onRoleSelected.next({
          role: role
        });
        this.loadingData = false;
        return role;
      }));
  }

  getAllRoleById(id): Observable<Role> {
    return this.webApiProvider.get<Role>(`role/v1/${id}`);
  }

  performAdvancedSearch(httpOptions?: any): void {
    this.loadingCollection = true;
    this.webApiProvider.getwithSearchParam('role/v1/list/0/6', httpOptions.params)
    .subscribe (response => {
      if (response) {
        this.onAdvancedSearchApplied.next({
          roles: response
        });
        this.loadingCollection = false;
      }
    },
     error => {
        this.toastr.error(error.errorMessage, 'Failed');
        this.loadingCollection = false;
     });
  }
}

