import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder } from '@angular/forms';
import { RoleService } from '../../../shared/services/roles.service';
import { HttpParams } from '@angular/common/http';

@Component({
  selector: 'app-roles',
  templateUrl: './roles.component.html',
  styleUrls: ['./roles.component.scss']
})
export class RolesComponent implements OnInit {

  public isCollapsed = true;

  advancedSearchForm: FormGroup;

  constructor(
    private fb: FormBuilder,
    private service: RoleService
  ) { }

  ngOnInit(): void {
    this.setAdvancedSearchForm();
  }

  setAdvancedSearchForm() {
    this.advancedSearchForm = this.fb.group({
      name: '',
      status: '',
      privilegeName: ''

    });
  }

  clear() {
    debugger;
    this.advancedSearchForm.controls['name'].setValue('');
    this.advancedSearchForm.controls['status'].setValue('');
    this.advancedSearchForm.controls['privilegeName'].setValue('');
    this.service.performAdvancedSearch();
  }

  performAdvancedSearch() {
    let name = this.advancedSearchForm.controls['name'].value;
    let status = this.advancedSearchForm.controls['status'].value;
    let privilegeName = this.advancedSearchForm.controls['status'].value;
    const httpOptions = {
      params: new HttpParams().set("name", name).set("status", status).set('privilegeName', privilegeName)
    };
    this.service.performAdvancedSearch(httpOptions);
  }

}
