import { Address, Attachment } from "../../model/common-model";

export class Role {

    id: number;
    name: string;
    status: string;
    createdBy:	string;
    createdDate: string;
    description: string;
    lastUpdatedDate: Date;
    updatedBy: string;
    userTypes: UserType[];
    version: number;
    privileges: Privilege[];

    public constructor(init?: Partial<Role>) {
        Object.assign(this, init);
    }
}

export class Privilege {
    createdBy: string;
    createdDate: Date;
    description: string;
    id: number;
    lastUpdatedDate: Date;
    name: string;
    status:	string;
    type: string;
    updatedBy: string;
    url: string;
    version: number;
}

export class RoleResponse {
    totalPages: number;
    totalElements: number;
    content: Role[];
}

export enum UserType {
    SYSTEM = 'System',
    INTERNAL = 'Internal',
    CLIENT = 'Client', 
    FINANCIAL = 'Financial',
    SERVICE = 'Services',
    TRANSPORTER = 'Transporter'
}